"use client";
import { useState } from "react";
import { useQuery } from "@tanstack/react-query";
import { api } from "@/lib/clientApi";
import Button from "@/components/Button";
import { DownloadIcon } from "@/components/Icon";

export default function ExportClient() {
  const base = api.defaults.baseURL?.replace(/\/$/, "") || "";
  const [from, setFrom] = useState("");
  const [to, setTo] = useState("");
  const [userId, setUserId] = useState("");
  const users = useQuery({ queryKey: ["users"], queryFn: async () => (await api.get(`/users`)).data });

  const qs = new URLSearchParams();
  if (from) qs.set("from", from);
  if (to) qs.set("to", to);
  if (userId) qs.set("userId", userId);
  const qstr = qs.toString();
  const summaryHref = `${base}/export/summary.csv${qstr ? "?" + qstr : ""}`;
  const ledgerHref = `${base}/export/ledger.csv${qstr ? "?" + qstr : ""}`;

  return (
    <section className="rounded-3xl border border-slate-200 dark:border-slate-800 bg-white/80 dark:bg-slate-900/70 p-6 shadow-lg space-y-4">
      <div className="grid grid-cols-1 sm:grid-cols-4 gap-3">
        <Field label="From date">
          <input type="date" className="input-like" value={from} onChange={(e) => setFrom(e.target.value)} />
        </Field>
        <Field label="To date">
          <input type="date" className="input-like" value={to} onChange={(e) => setTo(e.target.value)} />
        </Field>
        <Field label="Filter by member (optional)">
          <select className="input-like" value={userId} onChange={(e) => setUserId(e.target.value)}>
            <option value="">Everyone</option>
            {users.data?.map((u: any) => (
              <option key={u.id} value={u.id}>
                {u.name}
              </option>
            ))}
          </select>
        </Field>
        <div className="text-xs text-slate-500 dark:text-slate-400 flex items-end">Leave any field blank if you don’t need that filter.</div>
      </div>

      <div className="flex flex-col sm:flex-row gap-3">
        <a
          className="flex-1 inline-flex items-center justify-center gap-2 rounded-lg bg-blue-600 text-white px-4 py-2 text-sm font-medium hover:bg-blue-500 transition"
          href={summaryHref}
          target="_blank"
          rel="noreferrer"
        >
          <DownloadIcon className="w-4 h-4" /> Summary CSV
        </a>
        <a
          className="flex-1 inline-flex items-center justify-center gap-2 rounded-lg border border-blue-200 text-blue-700 px-4 py-2 text-sm font-medium hover:bg-blue-50 transition"
          href={ledgerHref}
          target="_blank"
          rel="noreferrer"
        >
          <DownloadIcon className="w-4 h-4" /> Detailed ledger
        </a>
      </div>

      <div className="rounded-2xl border border-slate-100 dark:border-slate-800 p-4 text-sm text-slate-600 dark:text-slate-300 bg-white/70 dark:bg-slate-900/50">
        <p className="font-medium text-slate-900 dark:text-white">What’s what?</p>
        <ul className="mt-2 space-y-1.5 list-disc pl-5">
          <li>Summary CSV = one row per member with deposits, withdrawals, and balance.</li>
          <li>Ledger CSV = every transaction, perfect for auditors or spreadsheets.</li>
          <li>Filters apply to both downloads. Use them to prep for monthly reviews.</li>
        </ul>
      </div>
    </section>
  );
}

function Field({ label, children }: { label: string; children: React.ReactNode }) {
  return (
    <div className="space-y-2">
      <label className="text-sm font-medium text-slate-600 dark:text-slate-300">{label}</label>
      {children}
    </div>
  );
}
